function calc() {
        //// Кулькулятор ////

            // Инпут для количества человек 
            let persons = document.querySelectorAll('.counter-block-input')[0],
            // Инпут для количества дней
        restDays = document.querySelectorAll('.counter-block-input')[1],
            // Селектор для выбора места 
        place = document.getElementById('select'),
            // Div с общей стоимостью
        totalValue = document.getElementById('total'),
            // Сумма количества человек
        personsSum = 0,
            // Сумма количества дней
        daysSum = 0,
            // Общая сумма, в сложении всех параметров
        total = 0;

        // Обнуляем значение поля общей суммы
    totalValue.innerHTML = 0;


    // Событие на первый инпут с людьми
    persons.addEventListener('input', function() {
            // Записываем сюда данные с инпута
        personsSum = +this.value;
            // Запрещаем все буквы и символы
        this.value = this.value.replace(/^[A-Z]+$/g, '');
            // Формула расчета - Сумма берется от заказчика и она сейчас рандомна
        total = (daysSum + personsSum) * 2;
            // Если 2 поле с днями пустое, то мы не расчитываем сумму
        if(restDays.value == '') {
            totalValue.innerHTML = 0;
        } else {
            totalValue.innerHTML = total;
        }
        return personsSum;
    });

        // Событие на второй инпут с днями
    restDays.addEventListener('input', function() {
        // Записываем сюда данные с инпута
    daysSum = +this.value;
        // Формула расчета - Сумма берется от заказчика и она сейчас рандомна
    total = (daysSum + personsSum) * 2;
        // Запрещаем все буквы и символы
    this.value = this.value.replace(/^[A-Z]+$/g, '');
        // Если 1 поле с людьми пустое, то мы не расчитываем сумму
    if(persons.value == '') {
        totalValue.innerHTML = 0;
    } else {
        totalValue.innerHTML = total;
    }
    return daysSum;
});


    // Функция сбрасывания общей стоимости, если один из инпутов пустой
    persons.addEventListener('input', function() {
        let a = this.value;
        let myString = a.toString();
        let newArray = myString.slice(0, 1);
        if (newArray[0] == 0) {
            persons.value = '';
        }
        if (this.value == '') {
            totalValue.innerHTML = 0;
        } 
});
    
        // Функция сбрасывания общей стоимости, если один из инпутов пустой
    restDays.addEventListener('input', function() {
        let c = this.value;
        let myString = c.toString();
        let newArray = myString.slice(0, 1);
        if (newArray[0] == 0) {
            restDays.value = '';
        }
        if (this.value == '') {
            totalValue.innerHTML = 0;
        } 
    });

        // Функция выбора города, с расчетом стоимости
    place.addEventListener('change', function() {
            // Проверям заполненность полей, если нет, то сбрасываем общюю стоимость
        if (restDays.value == '' || persons.value == '') {
            totalValue.innerHTML = 0; 
        } else {
                // Передаем сюда переменную total, сохранив ее значение для расчетов
            let a = total;
                // Находим select и выбираем его значение
            totalValue.innerHTML = a * this.options[this.selectedIndex].value;
        }
    });
}

module.exports = calc;