function slider() {
    //// Слайдер ////

            // Тот слайдер, который показывается в текущий момент
            let slideIndex = 1,
            // Сами слайды - фото, картинки
        slides = document.querySelectorAll('.slider-item'),
            // Навигация вперед - стрелка
        prev = document.querySelector('.prev'),
            // Навигация назад - стрелка
        next = document.querySelector('.next'),
            // Div с точками для переключения
        dotsWrap = document.querySelector('.slider-dots'),
            // Точки для переключения 
        dots = document.querySelectorAll('.dot');

            // Функция показа слайдов 
        showSlides(slideIndex);
       
        // Функция показа слайдов 
    function showSlides(n) {
            // n - номер слайдера, если больше общего количества, то возвращаемся в начало 
        if (n > slides.length) {
            slideIndex = 1;
        }   // Если мы листаем назад, то возвращаем предыдущий слайдер
        if (n < 1) {
            slideIndex = slides.length;
        }

            // берем массив со слайдами, и перебираем их отключая
        slides.forEach((item) => item.style.display = 'none');
            // Перебираем все точки под слайдером, и удаляем класс с подсветкой активной точки
        dots.forEach((item) => item.classList.remove('dot-active'));
            // Начали нумерацию с 0 и показали нужный слайдер из переменной
        slides[slideIndex - 1].style.display = 'block';
            // Начали нумерацию точек с 0 и присвоили активный класс нужной
        dots[slideIndex - 1].classList.add('dot-active');
    }

        // Функция увеличивающая параметр slideIndex 
    function plusSlides(n) {
            // В аргумент идет индекс, далее он увеличивается на 1 и вызывает функцию с новым параметром
        showSlides(slideIndex += n);
    }

        // Определяем текущий слайд и устанавливает его  
    function currentSlide(n) {
            // когда мы нажымаем на 4 точку, сюда передается 4 и нам показывает 4 слайдер
        showSlides(slideIndex = n);
    }

        // Уменьшаем слайд на 1
    prev.addEventListener('click', function() {
        plusSlides(-1);
    });
        // Увеличиваем слайд на 1
    next.addEventListener('click', function() {
        plusSlides(1);
    });

    dotsWrap.addEventListener('click', function(event) {
            // Добавляем 1 к массиву, что бы прировнять число js к реальному числу 
        for (let i = 0; i < dots.length + 1; i++) {
                // Проверяем содержит ли кнопка класс dot и имеет ли индекс, переводя его в js / i-1
            if (event.target.classList.contains('dot') && event.target == dots[i-1]) {
                    // Устанавливаем слайд на нужную точку
                currentSlide(i);
            }
        }
    });
}

module.exports = slider;