function modal() {
       //// Модальное окно на табах ////

        let overlay = document.querySelector('.overlay'), // Само модальное окно
        more = document.querySelector('.more'), // Кнопка вызова
        body = document.querySelector('body'); // Кнопка вызова

   let modalWindow = (event) => {
       let target = event.target;
           if (target && target.classList.contains('description-btn') || target.classList.contains('more')) {

           overlay.style.display = 'block';
           more.classList.add('more-splash');
           document.body.style.overflow = 'hidden';  
   }

           if (target && target.classList.contains('popup-close')) {
           
           overlay.style.display = 'none';
           more.classList.remove('more-splash');
           document.body.style.overflow = '';
   }
};

body.addEventListener('click', modalWindow);
}

module.exports = modal;