function formModal() {
    
    // // Модальное окно
 
    let message = {
        loading: 'Загрузка...', // При загрузке
        success: 'Спасибо, скоро мы с вами свяжемся! Хорошего дня!', // При Если все хорошо
        failure: 'Что-то пошло не так...' // Если ошибка
    };

    let formModal = document.querySelector('.main-form'), // Сама форма
        inputModal = formModal.getElementsByTagName('input'),  // Поле ввода
        statusMessage = document.createElement('div');  // Создаем новый div
            // Присваиваем новому диву заранее созданный класс
        statusMessage.classList.add('status');
    
           // Проверяю инпут, запрет на буквы, только цифры
        inputModal[0].addEventListener('input', function() {
        this.value = this.value.replace(/[^\d(?:+)]/ig, '');
            if (this.value.length > 10) {
                console.log('Слишком много цифр');
                // Здесь будет проверка
        }
    });

    function clearInput() {
        for (let i = 0; i < inputModal.length; i++) {
            inputModal[i].value = '';
        }
    }
   
    formModal.addEventListener('submit', function(event) {
        event.preventDefault();
        formModal.appendChild(statusMessage);
        postData()
            .then( () => statusMessage.innerHTML = message.loading)
            .then( () => statusMessage.innerHTML = message.success)
            .catch( () => statusMessage.innerHTML = message.failure)
            .then(clearInput);
    });

    function postData() {
    
        return new Promise(function(resolve, reject) {
            let formData = new FormData(formModal);
            let obj = {}; 
            formData.forEach(function(value, key) {
                obj[key] = value;
            });
            let jsonForm = JSON.stringify(obj);
                let request = new XMLHttpRequest();
                request.open('POST', 'server.php');
                request.setRequestHeader('Content-Type', 'application/json; charset=utf-8');                
                request.onreadystatechange = function() {
                    if (request.readyState < 4) {
                        resolve();
                    } else if (request.readyState === 4) {
                        if (request.status == 200 && request.status < 3) {
                            reject();
                        }
                        else {
                            resolve();
                        }
                    }
                };
                    request.send(jsonForm);
            });
    }

}

module.exports = formModal;

